/*header*/
.header {
  display: flex;
  align-items: center;
  padding: 30px 88px;
  background-color: $cAA0601;
  a {
    display: block;
    margin-right: auto;
  }
  img {
    display: block;
    width: 80px;
    height: 49px;
  }
  li {
    display: inline;
    margin: 0 32px;
    font: normal normal normal Noto Sans;
    font-size: 20px;
    line-height: 30px;
    color: #fff;
  }
}
/*header end*/
